import { Component, inject } from '@angular/core';
import { ChatRoomService } from '../../services/chat-room.service';
import { ActivatedRouteSnapshot } from '@angular/router';
import { ChatRoomData } from '@shared/types/shared.types';

@Component({
  selector: 'app-chat-room',
  standalone: true,
  imports: [],
  templateUrl: './chat-room.component.html',
  styleUrl: './chat-room.component.css'
})
export default class ChatRoomComponent {
  chatRoomService = inject(ChatRoomService);
  route = inject(ActivatedRouteSnapshot);

  chatRoom: ChatRoomData | null = null;

  ngOnInit() {
    this.chatRoom = this.route.data as ChatRoomData;
  }

}
